@use 'variables';

.event {
    padding: 80px 0;
    background-image: linear-gradient(180deg, red, purple);

    &--evento {
        background-image: linear-gradient(180deg, variables.$red1, variables.$purple);
    }

    &--festa {
    background-image: linear-gradient(180deg, variables.$purple, variables.$red2);
    }

    .container {
        display: flex;
        gap: 40px;
    }

    &__image {
        max-width: 360px;
    }

    &__details {
        &__title {
            font-size: 20px;
            font-weight: bold;
            margin-bottom: 16px;
        }

        &__description {
            line-height: 22px;
        }
    }

    @media (max-width: 640px) {
        .container {
            display: block;
        }

        &__image {
            width: 100%;
        }

        &__details {
            margin-right: 0;
            margin-bottom: 16px;
        }
    }
}